import React from 'react';
import { Grid } from '@material-ui/core';

const LargeTagsList = (props) => {
  const { items, position = 'left', md } = props;
  return (
    <Grid container justifyContent={position} spacing={1} md={md}>
      {items.map((Item, index) => {
        return (
          <Grid item key={index}>
            <Item />
          </Grid>
        );
      })}
    </Grid>
  )
};

export default LargeTagsList;