import React from 'react';
import {
  Box, Paper, Typography, Divider,
  Stepper, Step, StepLabel, StepContent,
} from '@material-ui/core';
import StepperLabel from '../common/StepperLabel';

const ExperienceStepper = (props) => {
  const { title, Icon, data } = props;
  return (
    <Box component={Paper} height="100%" p={2}>
      <Typography align="center" variant="h4" gutterBottom>
        {title}
      </Typography>
      <Divider light variant="middle" />
      <Stepper orientation="vertical">
        {data.map((item, index) => {
          const { title, date, position, supervisor, description } = item;
          return (
            <Step key={index} active expanded completed>
              <StepLabel
                StepIconComponent={() => (<Icon color="secondary" />)}
              >
                <StepperLabel
                  title={title} date={date}
                  detail={position} extraDetail={supervisor}
                  md1={9} md2={3}
                />
              </StepLabel>
              <StepContent>
                {description.map((item, index) => {
                  return (
                    <Box mb={-1.5}>
                      <Typography key={index} variant="body1" paragraph align='justify'>
                        {item}
                      </Typography>
                    </Box>
                  );
                })}
              </StepContent>
            </Step>
          )
        })}
      </Stepper>
    </Box >
  );
};

export default ExperienceStepper;
