import React from 'react';
import { Button } from '@material-ui/core';

const onDownload = () => {
  const link = document.createElement("a");
  // link.download = 'MyCV.pdf';
  link.target = "_blank";
  link.href = "MyCV.pdf";
  link.click();
};

const CV = (props) => (
  <Button
    variant="outlined"
    color="secondary"
    size="large"
    onClick={onDownload}
    {...props}
  >
    Download CV
  </Button>
);

export default CV;