import React from 'react';
import { Box } from '@material-ui/core';
import StepperTitle from './StepperTitle'
import StepperDetails from './StepperDetails'

const StepperLabel = (props) => {
  const { title, date, detail, extraDetail, md1, md2 } = props;
  return (
    <Box>
      <StepperTitle
        title={title}
        date={date}
        md1={md1}
        md2={md2}
      />
      <StepperDetails
        detail={detail}
        extraDetail={extraDetail}
      />
    </Box >
  );
};

export default StepperLabel;
