import React from 'react';
import { Box, Grid } from '@material-ui/core';
import SkillList from './SkillList';
import { leftSkills, rightSkills } from '../../data/skills';

const Skills = () => {
  return (
    <Grid container justifyContent="center">
      <Grid item md={6} sm={12}>
        <Box ml={3} mr={5}>
          <React.Fragment>
            {leftSkills.map((item, index) => {
              return (
                <SkillList
                  key={index}
                  title={item.title}
                  data={item.subSkills}
                />
              );
            })}
          </React.Fragment>
        </Box>
      </Grid>
      <Grid item md={6} sm={12}>
        <Box ml={5} mr={3}>
          <React.Fragment>
            {rightSkills.map((item, index) => {
              return (
                <SkillList
                  key={index}
                  title={item.title}
                  data={item.subSkills}
                />
              );
            })}
          </React.Fragment>
        </Box>
      </Grid>
    </Grid>
  );
};

export default Skills;