import React from 'react';
import { Grid, Box, Paper, Typography } from '@material-ui/core';
import Rating from '@material-ui/lab/Rating';
import SkillImage from './SkillImage';
import Tag from '../common/Tag';

const Skill = (props) => {
  const { title, url, level, rate } = props;
  return (
    <Grid item md={12}>
      <Box component={Paper} p={1}>
        <Grid container spacing={1}>
          <Grid container item alignContent='center' lg={1} md={1}>
            <SkillImage
              title={title}
              url={url}
            />
          </Grid>
          <Grid container item lg={4} md={6}>
            <Box alignItems="center" justifyContent="center" ml={3} mt={0.2}>
              <Typography variant="h6">
                {title}
              </Typography>
            </Box>
          </Grid>
          <Grid container item justifyContent='center' alignContent='center'
            lg={4} md={5}
          >
            <Tag
              TagIcon={null}
              label={level}
              img=''
            />
          </Grid>
          <Grid container item justifyContent='center' alignContent='center'
            lg={3} md={12}
          >
            <Tag
              TagIcon={null}
              label={
                <Box pt={0.4}>
                  <Rating
                    alignContent='center'
                    name="read-only"
                    value={rate}
                    size="small"
                    readOnly />
                </Box>}
              img=''
            />
          </Grid>
        </Grid>
      </Box >
    </Grid >
  );
};

export default Skill;