import React from 'react';
import {
  Box, Paper, Typography, Divider,
  Stepper, Step, StepLabel, StepContent,
} from '@material-ui/core';
import StepperLabel from '../common/StepperLabel';
import StepperDescription from '../common/StepperDescription';
import TwoColList from '../common/TwoColList';
import ThreeColList from '../common/ThreeColList';

const EducationStepper = (props) => {
  const { title, Icon, data } = props;
  return (
    <Box component={Paper} p={2} height="100%">
      <Typography align="center" variant="h4" gutterBottom>
        {title}
      </Typography>
      <Divider light variant="middle" />
      <Stepper orientation="vertical">
        {data.map((item, index) => {
          const {
            title, date, program, gpa,
            description, achievements, courses
          } = item;
          return (
            <Step key={index} active expanded completed>
              <StepLabel
                StepIconComponent={() => (<Icon color="secondary" />)}
              >
                <StepperLabel
                  title={title} date={date}
                  detail={program} extraDetail={gpa}
                  md1={8} md2={4}
                />
              </StepLabel>
              <StepContent>
                <StepperDescription
                  items={description}
                />
                {achievements.length !== 0 ?
                  <Typography>Achievements:</Typography> : null}
                <TwoColList
                  items={achievements}
                  mt={-1} ml={-1} mr={-2}
                  md1={9} md2={3}
                />
                {courses.length !== 0 ?
                  <Typography>Related Courses:</Typography> : null}
                <ThreeColList
                  items={courses}
                  mt={-1} ml={-1} mr={-2} mb={1}
                  md1={6} md2={4} md3={2}
                />
              </StepContent>
            </Step>
          )
        })}
      </Stepper>
    </Box >
  );
};

export default EducationStepper;