import React from 'react';
import { Box, Typography } from '@material-ui/core';

const StepperDescription = (props) => {
  const { items } = props;
  return (
    <Box>
      {items.map((item, index) => {
        return (
          <Box mb={-1.5}>
            <Typography key={index} paragraph variant="body1">
              {item}
            </Typography>
          </Box>
        );
      })}
    </Box>
  )
};

export default StepperDescription;